<v-app style="background-image: url('https://images-na.ssl-images-amazon.com/images/I/61hsPtI3qtL._AC_SX679_.jpg');background-position:center;background-repeat:no-repeat;background-size:cover;">
    <v-app-bar app  style="background-color:#00b3a7">
      
      <div class="d-flex align-center">
        <v-img alt="TotalPlay" class="shrink mr-2" contain min-width="50%" src="https://pdr.prod.totalplay.corp/images/total_play_logo.png" transition="scale-transition" width="170px"/>
      </div>
      <v-spacer></v-spacer>
        <div style="font-size: 21px;font-weight: bolder;color:white">Teoricos y promedios v1.0</div>
    </v-app-bar>

    <v-content >
      <section id="hero" >
        <v-row no-gutters>
          <!-- <v-img src="https://www.grupoamicizia.com.mx/upload/slide/6.jpg" style="height: 90%;"> -->
            <v-theme-provider >
              <v-container fill-height>
                  <v-row align="center" class="black--text mx-auto" justify="center">
                 <v-card  width="320" class="elevation-14" style="margin-top:60px">
                    <v-card-title  style="background-color:#00b3a7;border-color: #1E88E5;color:white">
                      <span class="headline" style="text-align:center;width: 100%;">Ingrese sus credenciales</span></v-card-title>
                    <v-card-text>
                    <v-container>
                        <!-- formulario -->
                        <v-card class="elevation-0">
                            <v-form ref="form" v-model="formLoginValido" lazy-validation style="font-weight: bolder;">
                                <v-row class="mx-auto"  dense style="margin-top:15px">
                                    <v-col cols="12" md="12" sm="12" class="">
                                        <v-text-field :rules="campoRequerido"   label="Usuario" solo  maxlength="100"  style="font-size:12px;"  v-model="$store.state.usuario"  required ></v-text-field>
                                    </v-col>
                                </v-row>
                                <v-row class="mx-auto"  dense style="margin-top:-15px">
                                    <v-col cols="12" md="12" sm="12" class="">
                                        <v-text-field  :rules="campoRequerido" type="password" solo label="Contraseña"    maxlength="100" style="font-size:12px" @keyup.enter="loginLdap"  v-model="$store.state.contrasenia"  required ></v-text-field>
                                    </v-col>
