                        </v-card>
                        <!-- formulario -->
                        <v-divider></v-divider>
                    </v-container></v-card-text>
                    <v-card-actions>
                    <v-spacer></v-spacer>
                  
                    <v-btn small dark color="#00b3a7"  @click="loginLdap" style="margin-right:22px">Ingresar</v-btn>
                    
                    </v-card-actions>
