</template>
<script>

//import operations from '../operations';
import router from '../router';

export default {
    name: 'Login',
  components:{
      
    },
    data(){
        return{
            formLoginValido:'',
            campoRequerido: [
             r => !!r || 'El campo es requerido'
             ],
            snackbar:false,
            colorSnack:'red',
            msgSanck:'Verifique los campos en rojo',
            mensaje:false,
            overlay:''
        }
    },
    methods:{ 
      redirije(path){
      console.log(path);
      router.push(path);
    },
      async loginLdap(){
        this.overlay = true;
        if(!this.$refs.form.validate()){
              this.snackbar=true;
              this.colorSnack='red';
              this.msgSanck='Todos los campos son requeridos';
              this.mensaje=false;
              this.overlay = false;
              return;
            }else{

              try{

                if(this.$store.state.usuario=="gvargas" && this.$store.state.contrasenia=="gvargas01"){
                    this.$store.state.autenticado=true;
                    this.redirije("/");
                }else{
                    this.overlay = false;
                    this.snackbar = true;
                    this.msgSanck="Error : Credenciales inválidas";
                    this.colorSnack='red';
                    this.$store.state.autenticado=false;
                    this.mensaje=false;
                }

                }catch(error){
                  
                    this.overlay = false;
                    this.snackbar = true;
                    this.msgSanck="Error : Credenciales inválidas";
                    this.colorSnack='red';
                    this.mensaje=false;
                }
              
          }
      },
     

    }
}
</script>